<template>
  <div class="jp">
    <div class="album py-5 bg-light">
      <div class="container">
        <div class="row">
          <div class="col-3">
            <div class="foodtitle">
              <a href="#" @click.prevent="alllinks ='所有種類'">
                <P>所有種類</P>
              </a>
              <a href="#" @click.prevent="link = '蒸煮類',alllinks =''">
                <P>蒸煮類</P>
              </a>
              <a href="#" @click.prevent="link = '快炒類',alllinks =''">
                <P>快炒類</P>
              </a>
              <a href="#" @click.prevent="link = '炸物類',alllinks =''">
                <P>炸物類</P>
              </a>
              <a href="#" @click.prevent="link = '其它類',alllinks =''">
                <P>其它類</P>
              </a>
            </div>
          </div>
          <div class="col-9">
            <div class="row">
              <div
                class="col-lg-4"
                v-for="( item,key ) in test"
                :key="item.key"
                v-if="link == `${ item.kind }` || alllinks ==`${ item.allkinds }`"
              >
                <div class="card mb-4 box-shadow">
                  <img class="card-img-top" :src="`${ item.url }`" alt="Card image cap">
                  <div class="card-body">
                    <p class="card-text">
                      {{ item.name }}
                      <span
                        class="badge badge-primary"
                        v-if="'蒸煮類' == `${ item.kind }`"
                      >{{ item.kind }}</span>
                      <span
                        class="badge badge-danger"
                        v-else-if="'快炒類' == `${ item.kind }`"
                      >{{ item.kind }}</span>
                      <span
                        class="badge badge-warning"
                        v-else-if="'炸物類' == `${ item.kind }`"
                      >{{ item.kind }}</span>
                      <span
                        class="badge badge-success"
                        v-else-if="'其它類' == `${ item.kind }`"
                      >{{ item.kind }}</span>
                    </p>
                    <div class="d-flex justify-content-between align-items-center">
                      <div class="btn-group">
                        <button
                          type="button"
                          class="btn btn-sm btn-outline-secondary"
                          @click="getProduct(item,key)"
                        >作法</button>
                        <button type="button" class="btn btn-sm btn-outline-secondary">Edit</button>
                      </div>
                      <small class="text-muted">{{ item.date }}</small>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      alllinks: "所有種類",
      link: "",
      test: [
        {
          name: "海鮮烏龍麵",
          url:
            "https://scm-assets.constant.co/scm/unilever/e9dc924f238fa6cc29465942875fe8f0/7d6bb60f-f6ae-4a7d-874d-573fdda8f374.jpg",
          date: "07 / 26",
          kind: "蒸煮類",
          allkinds: "所有種類"
        },
        {
          name: "生魚片壽司",
          url: "https://cdn2.ettoday.net/images/1289/d1289443.jpg",
          date: "11 / 15",
          kind: "其它類",
          allkinds: "所有種類"
        }
      ]
    };
  }
};
</script>