<template>
  <div class="chn">
    <div class="album py-5 bg-light">
      <div class="container">
        <div class="row">
          <div class="col-3">
            <div class="foodtitle">
              <a href="#" @click.prevent="alllinks ='所有種類'">
                <P>所有種類</P>
              </a>
              <a href="#" @click.prevent="link = '蒸煮類',alllinks =''">
                <P>蒸煮類</P>
              </a>
              <a href="#" @click.prevent="link = '快炒類',alllinks =''">
                <P>快炒類</P>
              </a>
              <a href="#" @click.prevent="link = '炸物類',alllinks =''">
                <P>炸物類</P>
              </a>
              <a href="#" @click.prevent="link = '其它類',alllinks =''">
                <P>其它類</P>
              </a>
            </div>
          </div>
          <div class="col-9">
            <div class="row">
              <div
                class="col-lg-4"
                v-for="( item,key ) in test"
                :key="item.key"
                v-if="link == `${ item.kind }` || alllinks ==`${ item.allkinds }`"
              >
                <div class="card mb-4 box-shadow">
                  <img class="card-img-top" :src="`${ item.url }`" alt="Card image cap">
                  <div class="card-body">
                    <p class="card-text">
                      {{ item.name }}
                      <span
                        class="badge badge-primary"
                        v-if="'蒸煮類' == `${ item.kind }`"
                      >{{ item.kind }}</span>
                      <span
                        class="badge badge-danger"
                        v-else-if="'快炒類' == `${ item.kind }`"
                      >{{ item.kind }}</span>
                      <span
                        class="badge badge-warning"
                        v-else-if="'炸物類' == `${ item.kind }`"
                      >{{ item.kind }}</span>
                      <span
                        class="badge badge-success"
                        v-else-if="'其它類' == `${ item.kind }`"
                      >{{ item.kind }}</span>
                    </p>
                    <div class="d-flex justify-content-between align-items-center">
                      <div class="btn-group">
                        <button
                          type="button"
                          class="btn btn-sm btn-outline-secondary"
                          @click="getProduct(item,key)"
                        >作法</button>
                        <button type="button" class="btn btn-sm btn-outline-secondary">Edit</button>
                      </div>
                      <small class="text-muted">{{ item.date }}</small>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- Modal -->
    <div
      class="modal fade"
      id="exampleModalCenter"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalCenterTitle"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLongTitle">{{ number }}.{{ modalData.name }}</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <pre><div class="modal-body">作法:{{ modalData.todo }}<loading v-if="show == isloading "></loading></div></pre>
          <div class="modal-footer">
            <button type="button" class="btn btn-primary" data-dismiss="modal">OK</button>
            <!-- <button type="button" class="btn btn-primary">OK</button> -->
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import loading from "./loading.vue";
import $ from "jquery";
export default {
  data() {
    return {
      show: true,
      isloading: false,
      link: "",
      alllinks: "所有種類",
      test: [
        {
          id: 1,
          name: "炒青菜",
          url:
            "https://tokyo-kitchen.icook.network/uploads/recipe/cover/90520/large_9d9e9c4467b13664.jpg",
          todo: "1.辣椒蒜頭爆香<br/>2.下蒜末",
          date: "11 / 30",
          kind: "快炒類",
          allkinds: "所有種類",
          todo: `
            1.辣椒蒜頭爆香
            2.下蒜末
            3.高麗菜梗先下鍋炒軟
            4.高麗菜下鍋大火快炒 下鹽巴試一下味道
            5.完成
              `
        },
        {
          id: 2,
          name: "麻婆豆腐",
          url:
            "https://tokyo-kitchen.icook.network/uploads/recipe/cover/135246/large_36dff5bcb5b8c4c6.jpg",
          date: "12 / 02",
          kind: "蒸煮類",
          allkinds: "所有種類",
          todo: `
            1.薑、蒜、蔥白、辣椒切成碎末後，下油鍋炒至有香味出來
            2.下豬絞肉炒至六、七分熟，加入豆瓣醬和糖，攪拌
            3.接著加入醬油、米酒和些許的水。水滾後下板豆腐，蓋上鍋蓋稍微燜煮1分鐘
            4.用鍋鏟稍微撥動一下豆腐（別把豆腐攪爛囉），下太白粉水勾芡
            5.完成`
        },
        {
          id: 3,
          name: "炸春捲",
          url:
            "https://tokyo-kitchen.icook.network/uploads/recipe/cover/158054/large_7bc8fd5d73700502.jpg",
          date: "12 / 12",
          kind: "炸物類",
          allkinds: "所有種類",
          todo: `
            1.將所有材料切幼絲，冬粉泡軟，用剪刀剪數下
            2.燒熱鍋後下油，炒所有餡料，下少許鹽，醬油，糖調味
            3.將一大匙餡料放在春捲皮上
            4.從下面捲上 再將左右兩側對折，再用蛋液糊口
            5.把油鍋燒熱，將春捲炸至金黃色便完成！
              `
        },
        {
          id: 4,
          name: "清蒸鱈魚",
          url:
            "https://tokyo-kitchen.icook.network/uploads/recipe/cover/14690/large_d25d412b1ee66220.jpg",
          date: "12 / 20",
          allkinds: "所有種類",
          kind: "蒸煮類",
          todo: `
            1.鱈魚洗淨並用廚房紙巾擦乾表面，將米酒倒入盤中，鱈魚兩面沾上米酒去腥
            2.將醬油淋在鱈魚片上，放上薑絲、蔥段及蒜片。 電鍋外鍋一杯水，蒸煮約20分鐘
            3.電鍋跳起後，拿走蔥段，放入捲蔥絲、辣椒絲再悶2分鐘即可
          `
        },
        {
          id: 5,
          name: "白菜滷",
          url:
            "https://tokyo-kitchen.icook.network/uploads/recipe/cover/207489/large_78b9d17d741399c2.jpg",
          date: "12 / 22",
          allkinds: "所有種類",
          kind: "蒸煮類",
          todo: `
            1.洗淨青蔥切段、蘿蔔及木耳切條、香菇切片狀、大白菜切塊、蒜切末
            2.醬油及蠔油、砂糖攪拌備用
            3.扁魚干先烤過後切碎備用(烤到扁魚乾微焦捲曲
            4.熱鍋下少許油先炒香蒜末
            5.再炒香紅蘿蔔及香菇到微軟
            6.放豬肉絲炒到變色後加入青蔥拌炒，拌炒時加胡椒粉調味
            7.放木耳及大白菜微炒後，倒入水及加入扁魚末
            8.再倒入醬油及蠔油稍微攪拌後，加鍋蓋以中火燜煮約20分鐘
            9.開鍋蓋後加入鹽調味，再倒入太白粉水勾芡拌勻，最後加黑醋及香油提味拌勻，續煮約5分鐘即可裝盤
          `
        },
        {
          id: 6,
          name: "水餃",
          url:
            "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSDi7O7IhnyCLEJBN04zApsvAFHChDGHW9z13WwY26wEZhaCQdpSw",
          date: "12 / 30",
          allkinds: "所有種類",
          kind: "蒸煮類",
          todo: `
            1.豬絞肉剁出黏性放置一個大盆子，高麗菜洗淨瀝乾切碎加入些許鹽巴調味靜置10分鐘(放置另一個碗)待用，
            同時，將薑切末放至與豬絞肉同盆子，蔥洗淨瀝乾切蔥花(放置另一個碗)待用
            2.取一塊超大紗布，將高麗菜放置紗布中把所有菜水擰乾後放置原本的碗，
            將蔥花倒入高麗菜淋上香油拌勻(為了保持香油的香氣)
            3.高麗菜+蔥花倒入豬絞肉盆裡，加入剩餘的調味料拌勻，蓋上保鮮膜放置冰箱約1小時待用
          `
        }
      ],
      modalData: {},
      number: {}
    };
  },
  methods: {
    getProduct(item, key) {
      const vm = this;
      vm.isloading = true;
      $("#exampleModalCenter").modal("show");
      vm.modalData = "";
      vm.number = "";
      console.log(vm.number);
      setTimeout(() => {
        vm.modalData = item;
        vm.number = key + 1;
      }, 500);
      setTimeout(() => {
        vm.isloading = false;
      }, 500);
    }
  }
};
</script>