              </a>
              <a href="#" @click.prevent="link = '快炒類',alllinks =''">
                <P>快炒類</P>
              </a>
              <a href="#" @click.prevent="link = '炸物類',alllinks =''">
                <P>炸物類</P>
              </a>
              <a href="#" @click.prevent="link = '其它類',alllinks =''">
                <P>其它類</P>
              </a>
            </div>
